.gridContainer {
    width: 100%;
}

.gridItem {
	display: flex;
	justify-content: center;
	align-items: center;

    .center {
        width: 62px;
        height: 62px;
        
        border-radius: 50%;
        
        background-color: #D9D9D9;
    }
}

.mouseControlCamera {
    width: 100%;
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: center;

    padding-right: 1px;

    border-radius: 4px;
    background-color: #D9D9D9;

    cursor: pointer;

    &:hover {
        background-color: hsl(31, 100%, 60%);
    }

    &.active {
        background-color: hsl(31, 100%, 60%);
    }
}