.page{
    display: flex;
    width: 100%;
    height: calc(100vh - 192px);
    margin: 94px 20px;
    background: inherit;
}

.pageFull{
    position: absolute;
    display: flex;
    width: calc(100vw - 20px);
    height: calc(100vh - 20px);
    margin: 10px;
    background: inherit;
    z-index: 1000;
}