.coords {
	width: 230px;
	height: 20px;
	position: absolute;

	display: flex;
	justify-content: space-around;

	padding: 15px 10px;
	margin: 20px;
	bottom: 0;
	border-radius: 10px;

	font-size: 16px;
	font-family: 'Inter';

	color: white;
	background-color: rgba(78, 78, 78, 0.8);

	z-index: 1000;
}