.container {
	width: 500px;
	height: 640px;

	display: flex;
	align-items: center;
	flex-direction: column;

	position: absolute;
	top: 50%;
	left: 50%;
	z-index: 1;
	transform: translate(-50%, -50%);

	box-shadow: 24px;

	font-family: 'Inter';
	background-color: var(--bgTheme);
}

.header {
	width: 90%;

	display: flex;
	justify-content: space-between;
	align-items: center;

	padding: 20px 23px;

	font-size: 25px;
	color: var(--colorTextNew);

	border-bottom: 1px solid #d3d3d3;
}

.content {
	width: 90%;
	height: 360px;
}

.fieldText {
	width: 100%;

	padding: 5px;
	box-sizing: border-box;

	font-size: 16px;

	border: 1px solid #cfcfcf;
}

.fieldText:hover {
	border: 1px solid #cfcfcf;
}

.nameInput {
	margin: 10px 0;
	font-size: 14px;
	color: var(--colorTextNew);
}

.closeIcon {
	width: 20px;
	height: 20px;

	cursor: pointer;
}
.modBtn{
	width: 90%;
	height: 40px;

	position: absolute;
	bottom: 0;
	left: 5%;
	z-index: 100;

	margin: 33px 0;
	border: none;

	background: #FF992C;
	color: #fff;

	font-size: 16px;
	font-family: Inter;

	cursor: pointer;
}
.modBtn:hover{
	background: #CC6900;
}

.ListCameras {
	width: 100%;
	height: 200px;

	overflow: auto;

	border: 1px solid #cfcfcf;
	background: var(--bgTheme);
	color: var(--colorText);
}

.List {
	display: flex;
	justify-content: space-between;
	align-items: center;

	position: relative;
	margin: 0;
	padding: 0px 5px;

	border: 0;
	border-radius: 0;
	
	outline: 0;
	vertical-align: middle;
	text-decoration: none;
	
	background-color: transparent;

	cursor: pointer;
}

.allSelect {
	height: 28px;

	display: flex;
	justify-content: space-between;
	align-items: center;

	padding: 0px 5px;

	cursor: pointer;
}