.wrapper {
  height: max-content;

  overflow-y: auto;
}
.gridContainer {
  width: 100%;

  display: flex;
  flex-direction: column;
}
.searchContainer{
  width: 100%;
  height: 50px;

  display: flex;
  align-items: center;

  background: var(--bgThemeSearch);
  border: 1px solid var(--bgThemeBorder);
  box-shadow: 0 1px 2px rgba(16, 24, 40, 0.05);

  margin-top: 10px;
  padding-left: 15px;
  margin-left: 15px;
  margin-right: 15px;
}
.listLink{
  display: flex;
  justify-content: center;
  align-items: center;

  font-weight: 600;
  font-size: 18px;
  line-height: 24px;

  color: var(--colorText);
}
.iconSettings{
  width: 28px;
  height: 28px;

  color: #CFCFCF;
}

.imgConditional{
  width: 14px;
  height: 14px;

  margin-right: 10px;
}
.table {
  width: 100%;
  height: 100%;

  overflow: auto;

  font-family: 'Inter', sans-serif;
  line-height: 24px;
  letter-spacing: 0.17px;
  text-transform: uppercase;

  background: var(--bgTheme);
}

.title{
  font-weight: 600;
  font-size: 24px;
  line-height: 30px;
  margin-left: 15px;
  color: var(--colorText);
}

.head{
  height: 58px;
  width: 100%;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  position: relative;

  font-family: 'Inter', sans-serif;
  font-weight: 500;
  font-size: 18px;

  color: #494949;
  background: var(--bgTheme);
  border-bottom: 0.5px solid var(--bgThemeBorder);
}
.iconBox{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.box{
  display: flex;
  justify-content: center;

  width: 100%;
}
.searchInput{
  width: 100%;
}
.searchContainerIcon{
  width: max-content;
  height: 25px;

  margin-top: 2px;
}