.controls{
    display: block;
    position: absolute;
    bottom: 0;
    width: 100%;
    z-index: 100;
}

.controlPlay{
    display: flex;
    justify-content: space-between;
    width: 96%;
    margin: 0 auto;
}

.time{
    display: flex; 
    align-items: center; 
    margin: 8px; 
    font-size: 20px;
    color: white;
}

.timeContrl{
    display: flex;
}

.switchItem {
	display: flex;
	align-items: center;
	border: 1px solid var(--bgThemeBorder);

	cursor: pointer;
}

.recordTime {
    width: 27px;
    height: 27px;

    display: flex;
	align-items: center;
    justify-content: center;

	cursor: pointer;
}

.selectTime {
    width: 40px;
    height: 80px;
    display: flex;
    flex-direction: column;
    position: absolute;
    z-index: 1000;
    padding: 0;
    bottom: 5px;
    right: 5px;
    border: 1px solid #CFCFCF;
    background-color: var(--bgTheme);
}

.settings{
    width: 200px;
    position: absolute;
    display: flex;
    flex-direction: column;
    right: 50px;
    bottom: 80px;
    font-family: 'Inter';
    font-size: 12px;
    align-items: center;
    background-color: var(--bgTheme);
    border: 1px solid #CFCFCF;
}

.set{
    width: 90%;
    height: 40px;

    display: flex;
    align-items: center;
    justify-content: space-between;

    color: var(--colorText);
}

.sliderVolume{
    position: absolute;
    bottom: 90%;
}

.lineSet{
    border: 1px solid #F5F7FB; 
    width: 90%;
}

select{
    border: none;
}

select::after{
    border: none;
}

select:hover{
    border: none;
}

.lifeClick {
    display: flex;
    align-items: center;

    margin: 10px;

    font-family: 'Inter';
    
    color: white;
    cursor: pointer;

    &:hover {
        color: rgba(255, 153, 44, 1);
    }
}