.container {
	width: 400px;
	height: 100px;

	display: flex;
	align-items: center;
	flex-direction: column;

	position: absolute;
	top: 50%;
	left: 50%;
	z-index: 2;
	transform: translate(-50%, -50%);

	font-family: 'Inter';
	background-color: var(--bgTheme);
	border: 1px solid var(--bgNewThemeBorder);
}

.header {
	display: flex;
	align-items: center;

	height: 50%;
	color: var(--colorText);
}

.buttons {
	display: flex;
	align-items: center;
	justify-content: space-between;

	height: 50%;
	width: 70%;
}

.cancel {
	color: rgb(207, 207, 207);

	cursor: pointer;
}

.delete {
	color: #ff2c2c;

	cursor: pointer;
}
