@import 'colors';

$themes: (
				bgThemeColor1: (
								darkTheme: $darkBlack,
								lightTheme: $grey,
				),
				bgThemeButton1: (
								darkTheme: $black,
								lightTheme: $white,
				),
				bgThemeButtonActivate: (
								darkTheme: $black00,
								lightTheme: $white,
				),
				bgThemeButtonActivateTwo: (
								darkTheme: $black00,
								lightTheme: $grey,
				),
				bgThemeSearch: (
								darkTheme: $black2,
								lightTheme: $white,
				),
				bgNewThemeBorder: (
								darkTheme: $borderDark,
								lighten: $borderNewLight,
				),
				bgThemeBorder: (
								darkTheme: $darkGrey,
								lightTheme: $borderLight,
				),
				bgThemeTitle: (
								darkTheme: $borderLight,
								lightTheme: $darkGrey,
				),
				bgButtonBorder: (
								darkTheme: $lightGrey,
								lightTheme: #d9d9d9,
				),
				bgButtonTextIcon: (
								darkTheme: #808080,
								lightTheme: #EDEDED,
				),
				bgTheme: (
								darkTheme: $black,
								lightTheme: $white,
				),
				bgThemeButtonIcon: (
								darkTheme: #494949,
								lightTheme: $borderLight,
				),
				bgButtonIcon: (
								darkTheme: #494949,
								lightTheme: $borderDark,
				),
				bgButtonIconSelect: (
								darkTheme: #494949,
								lightTheme: $black,
				),
				colorTextNotifications: (
								darkTheme: $lightGrey,
								lightTheme: $darkNewGrey,
				),
				colorTextTitleNotifications: (
								darkTheme: $borderDark,
								lightTheme: $titleColor,
				),
				colorHeader: (
								darkTheme: #222222,
								lightTheme: $grey,
				),
				colorText: (
								darkTheme: $white,
								lightTheme: $black00,
				),
				colorTextNew: (
								darkTheme: $newColorNewDark,
								lightTheme: $newColorNewLight,
				),
				colorTextActive: (
								darkTheme: $white,
								lightTheme: #ff992c,
				),
);

@mixin styles($mode) {
	@each $key, $map in $themes {
		@each $prop, $color in $map {
			@if $prop == $mode {
				--#{$key}: #{$color};
			}
		}
	}
}

.dark-mode {
	@include styles('darkTheme');
}

.light-mode {
	@include styles('lightTheme');
}