.container {
	width: fit-content;

	display: flex;
	justify-content: flex-start;

	position: relative;
	margin-left: 10px;

	.iconButton {
		height: 48px;
		width: 48px;
		border-radius: 0;
		background-color: hsl(31, 100%, 60%);
	}

	.body {
		width: 275px;
		height: fit-content;

		display: flex;
		justify-content: flex-start;

		position: absolute;
		right: 0px;
		top: calc(100% + 6px);
		z-index: 1000;

		border: 1px solid #d3d3d3;
		padding: 13px 18px;

		font-family: Inter, sans-serif;
		background-color: #fff;

		.bodyContainer {
			width: 100%;

			display: flex;
			flex-direction: column;
			align-items: flex-start;

			.bodyTitle {
				color: #667085;
				font-size: 18px;
				font-weight: 600;
			}

			.list {
				width: 100%;

				padding: 0;
			}

			.nestedList {
				padding: 8px 0;

				position: relative;
			}

			.item {
				display: flex;
				justify-content: flex-start;

				padding: 6px 0;
				padding-left: 8px;

				.nameItem {
					margin-left: 14px;

					color: #475467;
					font-size: 18px;
					font-weight: 400;
				}

				&.all {
					margin: 8px 0;
				}
			}
		}
	}
}
