@import '../../globalStyles/vars.module.scss';

.parent {
    width: 100vw;
	height: 100vh;

    .menu {
		position: fixed;

		top: calc($header-height + 2.5%);
		bottom: 2.5%;

		&.right {
			right: 0;
		}

		z-index: 1000;
		background-color: aqua;

		.item {
			margin: 4px 8px;
		}
	}
}

.container {
	width: 100%;
	height: calc(100% - $header-height);

	padding-top: $header-height;
	position: relative;
}

.errorMessagePosition {
	position: absolute;
	top: calc($header-height + 13px);
	left: 10px;
	z-index: 10000;

	span {
		white-space: pre;
		user-select: none;
	}

	.goToLookOut {
		color: hsl(31, 100%, 94%);
		text-decoration: none;

		&:hover {
			color: hsl(31, 100%, 60%);
		}
	}
}