.cardsBox{
  height: 49%;

  display: flex;
  justify-content: space-between;
}
.cardBox{
  width: 49%;
}
.head{
  height: 58px;
  width: 100%;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  position: relative;
  font-family: 'Inter', sans-serif;
  font-weight: 500;
  font-size: 18px;
  color: #494949;
  background: var(--bgTheme);
  border-bottom: 0.5px solid var(--bgThemeBorder);
}
.wrapper {
  height: calc(100% - 60px);

  overflow: auto;
  background: var(--bgThemeColor1);
}
.smallTable{
  width: 100%;
  height: 100%;

  display: flex;
  justify-content: center;
  align-items: center;

  background: var(--bgTheme);
  color: #CFCFCF;
}
.title{
  font-weight: 600;
  font-size: 24px;
  line-height: 30px;
  margin-left: 15px;

  color: var(--colorText);
}
