@import '../../../../Theme/light-dark-theme';

.reconnectMenu {
	height: 100%;

	display: flex;
	flex-direction: column;
	justify-content: space-between;
	overflow-y: auto;
}

.titleBox {
	padding: 14px 21px 6px 10px;
}

.title {
	font-family: Inter, sans-serif;
	font-weight: 500;
	font-size: 18px;
	color: var(--colorText);

	line-height: 1.5;
	letter-spacing: 0.00938em;
}

.list {
	display: flex;
	flex-direction: column;

	.roundIndicator {
		margin-right: 10px;
	}

	.recName {
		font-family: Inter, sans-serif;
		font-weight: 400;
		font-size: 16px;
		color: var(--colorText);
	}
}

.information {
	font-weight: 400;
	font-size: 12px;
	color: var(--colorText);
}

.confirmButtons {
	display: flex;

	gap: 12px;
	margin-bottom: 10px;

	.save {
		height: 54px;

		flex-grow: 1;

		border-radius: 0;

		color: #fefefe;
		font-size: 16px;
		font-family: Inter, sans-serif;

		&.on {
			background-color: hsl(31, 100%, 60%);

			&:hover {
				background-color: hsl(31, 100%, 40%);
			}
		}

		&.off {
			background-color: hsl(0, 0%, 45%);

			&:hover {
				background-color: hsl(0, 0%, 62%);
			}
		}
	}

	.cansel {
		height: 54px;

		flex-grow: 1;

		border-radius: 0;

		color: #989898;
		font-size: 18px;
		font-family: Inter;

		background-color: var(--bgTheme);

		&:hover {
			background-color: hsl(31, 100%, 80%);
		}
	}
}
