@import '../../../../../Theme/light-dark-theme';

.container {
	width: 420px;
    height: fit-content;

	padding: 13px 20px 20px;

	background-color: var(--bgTheme);

    .contentPosition {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }

	.modalHeader {
		display: flex;
		justify-content: space-between;
		align-items: center;

		padding-bottom: 10px;

		position: relative;
		
		&::after {
			height: 1px;

			display: block;

			content: '';

			position: absolute;

			left: -20px;
			bottom: 0px;
			right: -20px;
			z-index: 2;

			background-color: var(--bgButtonBorder);
		}
	}

	.modalTitle {
		position: relative;

		color: var(--colorText);
		font-family: Inter, sans-serif;
		font-weight: 500;
		font-size: 24px;
        
        user-select: none;
	}

	.inputLabel {
		color: var(--colorText);
		font-family: Inter, sans-serif;
		font-weight: 400;
		font-size: 14px;
        
        user-select: none;
	}

	.iconButton {
		padding: 0px;
		border-radius: 0;

		margin-right: 0;
	}

	.buttonBox {
		display: flex;
		flex-wrap: wrap;

		margin-top: 40px;

		gap: 12px;

		.create {
			height: 54px;

			color: #fff;
			font-size: 20px;
            font-family: Inter;

			border-radius: 0;

			background-color: hsl(31, 100%, 60%);

			&:hover {
				background-color: hsl(31, 100%, 40%);
			}
		}

		.cansel {
			height: 35px;

			color: #989898;
			font-size: 16px;
            font-family: Inter;

			border-radius: 0;

			background-color: var(--bgTheme);
		}
	}
}
