@import "src/Theme/light-dark-theme";
@import "src/Theme/colors";

.parent {
    display: flex;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    overflow: auto;
    background-color: var(--bgThemeColor1);
    color: var(--colorText);
}

.item{
    height: 100%;
    background: white;
    border: 1px solid #CFCFCF;
}

.App {
    height: 100%;
    width: 100%;
}