.container {
	width: 400px;

	display: flex;
	align-items: center;
	flex-direction: column;

	position: absolute;
	top: 50%;
	left: 50%;
	z-index: 2;
	transform: translate(-50%, -50%);

	font-family: 'Inter';
	background-color: var(--bgTheme);
	border: 1px solid var(--bgNewThemeBorder);
}
.modBtn {
	width: 100%;
	height: 100%;

	border-radius: 0;
	border: none;
	box-shadow: none;

	background: #ff992c;
	color: #fff;

	font-size: 16px;
	font-family: Inter;

	cursor: pointer;
}
.modBtn:hover {
	background: #cc6900;
}

.header {
	width: 90%;

	display: flex;
	justify-content: space-between;
	align-items: center;

	padding: 20px 20px;

	font-size: 25px;
	color: var(--colorTextNew);

	border-bottom: 1px solid #d3d3d3;
}

.content {
	width: 90%;

	margin-top: 10px;
}

.closeIcon {
	width: 20px;
	height: 20px;

	cursor: pointer;
}

.button {
	width: 100%;
	height: 45px;

	margin: 20px 0;
}
