@import '../../../../Theme/colors';
@import '../../../../Theme/light-dark-theme';

.containerMenu {
	overflow: auto;
}

.list {
	width: 100%;

	.rcItem {
		width: 100%;
		flex-grow: 1;

		padding: 12px 21px 12px 10px;

		.workIndicator {
			width: 8px;
			height: 8px;

			content: none;
			margin: 0 10px;

			border-radius: 50%;
		}

		.itemText {
			font-family: 'Inter';
			font-weight: 500;
			font-size: 18px;
			color: var(--colorText);
		}

		.itemIcon {
			justify-content: right;
			margin: 0 10px;
		}
	}
}

.emptyBox {
	width: 100%;

	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.item {
	display: flex;
	flex-grow: 1;

	padding: 12px 21px 12px 10px;

	.itemText {
		font-family: 'Inter';
		font-weight: 400;
		font-size: 16px;
		color: var(--colorText);
	}
}

.itemSelect {
	color: var(--colorText);
}