.error {
	width: 270px;
	
	position: absolute;
    top: 50%;
    left: 50%;

	z-index: 100;

	display: flex;
	flex-direction: column;
	align-items: center;

	font-family: 'Inter';

	transform: translate(-50%, -50%);
}

.erTitle {
	margin: 0;
	padding: 0;

	font-size: 20px;
	color: #adadad;
}

.erMessage {
	margin: 0;
	padding: 0;

	font-size: 17px;

	color: #ff992c;
	cursor: pointer;
}