@import '../../Theme/light-dark-theme';

.searchMenu {
	width: 100%;
	height: 100%;

	max-width: 375px;

	display: flex;
	flex-direction: column;

	position: relative;

	background-color: var(--bgTheme);
}

.btnPos {
	position: absolute;

	top: 5%;

	&.left {
		left: 100%;
	}
	&.right {
		right: 100%;
	}

	z-index: 1;

	.iconButton {
		width: 72px;
		height: 62px;
		background-color: #98a2b3;
		margin-bottom: 8px;
		border-radius: 0;

		&:hover {
			background-color: hsl(31, 100%, 80%);
		}

		&.active {
			background-color: hsl(31, 100%, 60%);
		}
	}
}

.container {
	width: 355px;
	height: 100%;

	margin: 10px;
	font-family: Inter;

	.titleBox {
		display: flex;
		justify-content: space-between;

		margin: 6px 0;

        .menuTitle {            
            font-size: 25px;
            font-family: Inter;
            color: var(--colorText);
        }

		.iconButton {
            border-radius: 0;
			color: var(--bgThemeButtonIcon);
		}
	}

	.divider {
		background-color: var(--bgThemeBorder);
	}

	.textField {
		padding: 5px 8px 5px 12px;
		border-radius: 0;
		background-color: #fff;
	}

	.emptyBox {
		width: 100%;

		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	.list {
		width: 100%;

		padding: 6px 0px;
		overflow-y: auto;

		.item {
			width: 100%;
			flex-grow: 1;

			padding: 8px 0px;

			.workIndicator {
				width: 8px;
				height: 8px;

				content: none;
				margin: 0 10px;

				border-radius: 50%;
			}

			.itemText {
				font-family: Inter;
				font-weight: 500;
                color: var(--colorText);
			}

			.itemIcon {
				justify-content: right;
				margin: 0 10px;
			}
		}
	}
}
