.item {
	position: relative;
	display: flex;
	height: 100%;
	cursor: pointer;
	background: white;
	border: 1px solid #cfcfcf;
	:hover .iconCheck {
		display: block;
	}
}

.itemFull {
	position: relative;
	display: flex;
	height: 100%;
	cursor: pointer;
	background: white;
	border: 1px solid #cfcfcf;
	.iconCheck {
		display: block;
	}
}

.block {
	position: relative;
	display: flex;
	width: 100%;
	height: 100%;
	background: #cfcfcf;
}

.player {
	position: absolute;
	width: 100%;
	height: 100%;
}

.nameItem {
	position: absolute;
	margin: 10px;
	z-index: 100;
	color: white;
	font-size: 14px;
	font-family: 'Inter';
	font-weight: 500;
}

.lowerInfo {
	width: calc(100% - 20px);

	position: absolute;
	display: flex;
	justify-content: space-between;
	align-items: center;

	bottom: 0;
	padding: 10px;
	
	font-family: 'Inter';
	font-weight: 300;
	font-size: 12px;
	z-index: 100;
	color: white;
}

.info {
	display: flex;
	flex-direction: column;
}

.iconCheck {
    width: 25px;
	height: 25px;

	display: none;

	position: absolute;
	right: 0;
    z-index: 1;

	margin: 10px;

	background-color: white;

	cursor: pointer;
}
