.page{
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: row;

  font-family: 'Inter', sans-serif;
  background: var(--bgTheme);
  color: var(--colorText);
}

.wrapperPerson{
  width:45%;
  height: 90%;

  margin-top: 20px;
  margin-left: 16px;
  margin-right: 20px;
}

.paper{
  display: flex;
  flex-direction: column;

  margin-top: 20px;
}

.text{
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 24px;
  margin-bottom: 20px;
  color: var(--colorText);
}

.secondText{
  color: var(--colorText);
  font-family: 'Inter', sans-serif;
  font-weight: 200;
  font-style: normal;
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 10px;
}