.cardField {
  width: calc(100% - 60px);
  height: 84%;

  position: fixed;
  top: 100px;
  left: 0;
  z-index: 2;
  margin: 0 30px;

  border: 0.5px solid var(--bgThemeBorder);
  background: var(--bgTheme);
}

.cardField * {
  font-family: 'Inter', sans-serif;
  font-weight: 500;
  font-size: 18px;
}

.table {
  width: calc(100% - 50px);
  height: calc(100% - 130px);

  overflow: auto;
  margin: 15px 25px;
}

.headReport {
  width: calc(100% - 15px);
  height: 95px;

  display: flex;
  justify-content: space-between;

  padding-left: 15px;
  border-bottom: 1px solid var(--bgThemeBorder);
  color: var(--bgThemeTitle);
}

.textAndSelect {
  width: 1120px;

  display: flex;
  align-items: center;
  justify-content: space-between;

  margin-right: 20px;
  p {
    margin-right: 20px;
  }
}

.check {
  height: 30px;
  width: 30px;

  display: flex;
  align-items: center;
  justify-content: center;

  border: 1px solid var(--bgThemeBorder);
}

.icons {
  width: 280px;

  display: flex;
  align-items: center;
  justify-content: space-between;

  padding-right: 25px;
}

.modLink {
  text-decoration: none;
  color: var(--bgThemeTitle);

  &:hover{
    color: #494949;
  }
}

.noData {
  height: 100%;
  margin: 0;

  display: flex;
  justify-content: center;
  align-items: center;

  font-weight: 600;
  font-size: 24px;
  letter-spacing: 2.17px;
  text-transform: uppercase;
  color: #CFCFCF;
}