@import "colors";
@import "light-dark-theme";

.btn-theme {
  height: 20px;

  display: flex;
  justify-content: flex-start;
  align-items: flex-start;

  font-family: 'Inter', sans-serif;
  line-height: 24px;
  letter-spacing: 0.17px;
  text-transform: uppercase;
  font-weight: 400;

  padding-top: 10px;
  text-align: start;

  cursor: pointer;

  color: var(--textThemeColor1);
  border: none;
}
