.container {
	width: 700px;

	display: flex;
	align-items: center;
	flex-direction: column;

	position: absolute;
	top: 50%;
	left: 50%;
	z-index: 1;
	transform: translate(-50%, -50%);

    border: 1px solid #475467;

	font-family: 'Inter';
	background-color: #f5f7fb;
}

.header {
	width: 90%;

	display: flex;
	justify-content: space-between;
	align-items: center;

	padding: 20px 23px;

	font-size: 25px;
    font-weight: 600;
}

.content {
	width: 90%;
	margin-bottom: 20px;
}

.closeIcon {
	width: 20px;
	height: 20px;

	cursor: pointer;
}