.choose {
	width: calc(100% - 20px);
    display: flex;
	justify-content: space-between;
    
	position: absolute;
	
	margin: 10px;
	font-size: 18px;
}

.chooseCamera {
	display: flex;
	align-items: center;
	cursor: pointer;
}

.chooseCamera:hover .moreIcon {
	display: block;
}

.selectHeader {
	display: flex;
	align-items: center;

	padding: 5px;

	font-family: 'Inter';
	font-size: 18px;

	z-index: 100; 
}

.nameCamera {
	margin: 5px;
}

.ListCameras {
	width: 270px;
	height: 500%;
	
	position: absolute;
	top: 43px;

	overflow: auto;
	z-index: 1000;

	background: var(--bgTheme);
	color: var(--colorText);
}

.SearchCameras {
	width: calc(100% - 10px);
	height: 44px;

	padding: 0px 5px;

	display: flex;
	align-items: center;
}

.moreIcon {
	display: none;
	width: 14px;
	height: 7px;
	z-index: 100;
}

.List {
	border: 0;
	margin: 0;
	display: flex;
	outline: 0;
	padding: 10px 5px;
	justify-content: space-between;
	position: relative;
	align-items: center;
	user-select: none;
	border-radius: 0;
	vertical-align: middle;
	text-decoration: none;
	background-color: transparent;
	cursor: pointer;
}

.List:hover {
	.edit {
		display: block;
	}
}

.edit {
	display: none;
	font-size: 12px;
	margin-right: 10px;
	font-family: 'Inter';
	color: #a4a4a4;
}

.workImg {
	width: 14px;
	height: 14px;

	margin: 5px;
}

.lifeClick {
    display: flex;
    align-items: center;

    margin: 10px;

    font-family: 'Inter';
    
	color: #fff;
	z-index: 100;
    cursor: pointer;

	&.live {
		color: #ff0000;
	}

    &:hover {
        color: #ff0000;
    }
}
