@import '../../../../Theme/light-dark-theme';

.presetMenu {
	height: 100%;

	display: flex;
	flex-direction: column;
	justify-content: space-between;
	overflow-y: auto;

	.confirmButtonsPos {
		margin-bottom: 10px;
	}

	.titleBox {
		display: flex;
		justify-content: space-between;
		align-items: center;

		padding: 5px 16px 0 10px;

		.iconButton {
			border-radius: 0;
		}
	}
	color: hsl(31, 100%, 40%);
}

.title {
	font-family: Inter, sans-serif;
	font-weight: 500;
	font-size: 18px;
	color: var(--colorText);

	line-height: 1.5;
	letter-spacing: 0.00938em;
}

.list {
	display: flex;
	flex-direction: column;

	.presName {
		font-family: Inter, sans-serif;
		font-weight: 400;
		font-size: 16px;
		color: var(--colorText);
	}

	.listItemIcon {
		display: flex;
		justify-content: flex-end;

		transition: opacity 0.2s;

		&.settings {
			opacity: 0;

			&:hover {
				.editText {
					color: hsl(31, 100%, 60%);
				}
			}
		}
	}

	.switchItem {
		display: flex;
		align-items: center;
		border: 1px solid var(--bgThemeBorder);
	}
	
	.editText {
		font-family: Inter, sans-serif;
		font-size: 10px;
		color: #B8B8B8;
		
		user-select: none;
		cursor: pointer;
	}
	.settingsItem {
		&:hover {
			.listItemIcon.settings {
				opacity: 1;
			}
		}
	}
}

.information {
	font-weight: 400;
	font-size: 14px;
}

.containerButtons {
	display: flex;
	flex-direction: column;

	text-align: center;

	gap: 12px;
}
.confirmButtons {
	display: flex;

	gap: 12px;

	.save {
		height: 54px;

		flex-grow: 1;

		border-radius: 0;

		color: #fff;
		font-size: 20px;
		font-family: Inter;

		background-color: hsl(31, 100%, 60%);

		&:hover {
			background-color: hsl(31, 100%, 40%);
		}
	}

	.cansel {
		height: 54px;

		flex-grow: 1;

		border-radius: 0;

		color: #989898;
		font-size: 18px;
		font-family: Inter;

		background-color: var(--bgTheme);

		&:hover {
			background-color: hsl(31, 100%, 80%);
		}
	}
}
