.confirmButtons {
	display: flex;

	gap: 12px;

	.save {
		height: 54px;

		flex-grow: 1;

		border-radius: 0;

		color: #fff;
		font-size: 20px;
		font-family: Inter;

		background-color: hsl(31, 100%, 60%);

		&:hover {
			background-color: hsl(31, 100%, 40%);
		}
	}

	.cansel {
		height: 54px;

		flex-grow: 1;

		border-radius: 0;

		color: #989898;
		font-size: 18px;
		font-family: Inter;

		background-color: var(--bgTheme);

		&:hover {
			background-color: hsl(31, 100%, 80%);
		}
	}
}