.coords {
    width: 250px;
	height: 20px;

	display: flex;
	justify-content: space-between;
    align-items: center;

    position: absolute;
    right: 100px;
    margin: 30px 0;
	z-index: 100;

	padding: 15px;

	font-size: 16px;

    transform: rotate(-180deg);

	color: white;
	background-color: rgba(78, 78, 78, 0.8);

    &.edit {
        width: 330px;
    }

    &.full {
        width: 450px;
    }
}

.inputCoords {
    width: 80px;
    text-align: center;

    appearance: none;
    -webkit-appearance: none; 
    border: none;
    background-color: transparent;
    padding: 0;
    margin: 0;
    font-size: inherit;
    font-family: inherit;
    color: inherit;
    user-select: none;

    &:focus-visible {
        border: none;
        outline: none;
    }

    &.formatTime {
        width: 30px;
    }
}

.pointsLabel {
    width: 80px; 
    text-align: center;
}

.pen {
	width: 15px;
	height: 15px;

	cursor: pointer;
}

.btnToggle {
    height: 20px;
	width: 20px;

    display: flex;
    margin-left: 10px;	
    justify-content: center;
    align-items: center;

	border: 1px solid white;
	background: none;
    color: white;
	cursor: pointer;
}