.tableBox * {
  font-family: 'Inter', sans-serif;
  line-height: 24px;
  letter-spacing: 0.17px;
  text-transform: uppercase;
}

.headFont {
  margin-left: 10px;

  text-align: left;
  font-weight: 400;
  color: #A4A4A4;
}

.bodyBig {
  min-width: 80px;
  margin: 0 10px 10px 0;
  font-weight: 500;
}

.table {
  width: 100%;

  color: var(--colorText);
  user-select: none;

  &.small {
    font-size: 14px;
  }

  &.large {
    font-size: 16px;
  }
}

.imgLargeTable {
  height: 17vh;
  width: auto;

  margin: 0 1vh 1vh 0;

  @media screen and (max-height: 960px) {
    height: 14vh;
  }

  @media screen and (max-height: 800px) {
    height: 10vh;
  }
}

.imgSmallTable {
  height: 82px;
  width: 157px;
}

.audioPlayer {
  display: flex;
  margin: 0 0 10px 5px;
}

.closeBtn {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
}

.fullImg {
  width: 75%;
  height: max-content;

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 3;
}
