.boxCard{
    width: 320px;
    height: max-content;

    position: absolute;
    left: 15px;
    bottom: 85px;
    z-index: 10000;

    background: white;
    border: 1px solid white;
    box-shadow: 0 12px 16px -4px rgba(16, 24, 40, 0.08), 0px 4px 6px -2px rgba(16, 24, 40, 0.03);
}

.text{
    width: 210px;

    font-family: Inter;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;

    color: var(--colorText);
}
.list{
    width: 320px;
    height: 165px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    font-family: Inter, serif;

    background: var(--bgTheme);
}
.listBtn{
    width: 300px;

    display: flex;
    flex-direction: row;
    align-items: center;

    padding: 10px 14px;
    gap: 8px;

    cursor: pointer;
}

