.card * {
    font-family: 'Inter', sans-serif;
    font-weight: 500;
}

.card {
    background: var(--bgTheme);
    border: 0.5px solid var(--bgThemeBorder);
}

.head {
    width: 100%;
    height: 58px;

    display: flex;
    align-items: center;
    justify-content: space-between;

    font-size: 18px;
    color: var(--bgThemeTitle);

    border-bottom: 1px solid var(--bgThemeBorder);
}

.nameCard {
    margin: 15px;
}

.icons {
    width: 80px;
    margin-right: 10px;

    position: relative;
}

.wrapper {
    height: calc(37vh - 80px );
    margin: 0 10px;
    overflow: auto;

    @media screen and (max-height: 950px) {
        height: calc(37vh - 100px);
    };

    @media screen and (max-height: 800px) {
        height: calc(38vh - 120px);
    };
}

.noData {
    height: 100%;
    margin: 0;

    display: flex;
    justify-content: center;
    align-items: center;

    font-weight: 500;
    font-size: 24px;
    letter-spacing: 2.17px;
    text-transform: uppercase;
    color: #CFCFCF;
}