.container {
	width: 500px;
	height: 250px;

	display: flex;
	align-items: center;
	flex-direction: column;

	position: absolute;
	top: 50%;
	left: 50%;
	z-index: 2;
	transform: translate(-50%, -50%);

	font-family: 'Inter';
	background-color: var(--bgTheme);
	border: 1px solid var(--bgNewThemeBorder);

	padding-bottom: 20px;
}

.titleStyle {
	display: flex;
	justify-content: center;
	align-items: center;
	align-content: center;

	font-family: 'Inter';
	font-size: 20px;
	color: var(--colorText);
}
.btnContainer {
	width: 90%;
	display: flex;
	justify-content: space-around;
}
.styleBtn {
	width: 130px;
	height: 50px;

	display: flex;
	justify-content: center;
	align-items: center;

	background-color: #ff992c;
	color: var(--colorText);

	font-family: 'Inter';
	font-size: 20px;
	border-radius: 5px;
	cursor: pointer;
}

.content {
	width: 90%;
	height: 360px;

	margin-top: 10px;
}
