.container {
	width: 500px;
	height: 750px;

	display: flex;
	align-items: center;
	flex-direction: column;

	position: absolute;
	top: 50%;
	left: 50%;
	z-index: 2;
	transform: translate(-50%, -50%);

	font-family: 'Inter';
	background-color: var(--bgTheme);
	border: 1px solid var(--bgNewThemeBorder);
}
.modeBtn {
	width: 90%;
	height: 50px;

	display: flex;
	justify-content: center;
	align-content: center;
	align-items: center;

	bottom: 0;
	left: 5%;
	margin: 40px 0;

	position: absolute;

	border-radius: 0;
	box-shadow: none;

	font-size: 20px;
	font-family: Inter;

	background: #ff992c;
	color: #fff;

	cursor: pointer;
}
.modeBtn:hover {
	box-shadow: none;
	background: #ff992c;
}
.header {
	width: 90%;

	display: flex;
	justify-content: space-between;
	align-items: center;

	padding: 20px 25px;

	font-size: 25px;
	color: var(--colorTextNew);

	border-bottom: 1px solid #d3d3d3;
}

.content {
	width: 90%;
	height: 360px;

	margin-top: 10px;
}

.fieldText {
	width: 100%;

	padding: 5px;
	box-sizing: border-box;

	font-size: 16px;
	border: 1px solid var(--bgThemeBorder);
	background: var(--bgThemeSearch);
}

.fieldText:hover {
	border: 1px solid var(--bgThemeBorder);
}

.nameInput {
	margin: 10px 0;

	font-size: 14px;
	color: var(--colorTextNew);
}

.check {
	margin: 30px 0;

	color: var(--colorTextNew);
	font-size: 16px;
}

.closeIcon {
	width: 20px;
	height: 20px;

	cursor: pointer;
}
