.headPage {
  width: calc(100% - 60px);
  height: 110px;

  display: flex;
  align-items: center;
  justify-content: space-between;

  padding: 0 30px;

  letter-spacing: 0.02em;
  color: var(--bgThemeTitle);
}

.namePage {
  font-family: 'Inter', sans-serif;
  font-weight: 600;
  font-size: 28px;
}

.bodyPage {
  width: 100%;
  height: calc(100% - 200px);

  position: absolute;
  top: 110px;
}

.footer {
  width: 100%;
  height: 70px;

  display: flex;
  justify-content: center;
  align-items: center;

  position: fixed;
  bottom: 0;
  z-index: 10;

  background-color: var(--bgTheme);
  box-shadow: 0 0 0 4px var(--bgThemeBorder);
}

.cards {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  grid-gap: 12px 15px;

  padding: 0 30px;
}