@import '../../../../../../Theme/colors';
@import '../../../../../../Theme/light-dark-theme';

.selectionBlock {
	display: flex;
	align-items: center;

	margin: 10px 20px;

	cursor: pointer;
}

.selectionItem {
	margin-left: 20px;

	font-size: 21px;
	font-family: 'Inter';

	color: var(--colorText);
}

.switchItem {
	display: flex;
	align-items: center;
	border: 1px solid var(--bgThemeBorder);

	cursor: pointer;
}

.iconCheck {
	width: 30px;
	height: 30px;
	
	cursor: pointer;
}