.drawer {
	width: 70px;

	display: flex;
	flex-direction: column;
	align-items: center;

	right: 0;
	margin-top: -30%;
}

.btnPos {
	position: absolute;

	top: 5%;
	z-index: 1;

	&.right {
		right: 100%;
	}


	.itemIcon {
		width: 72px;
		height: 62px;

		display: flex;
		align-items: center;
		justify-content: center;

		margin-bottom: 8px;

		cursor: pointer;
		background-color: #98a2b3;

		&:hover {
			background-color: hsl(31, 100%, 80%);
		}

		&.active {
			background-color: hsl(31, 100%, 60%);
		}

		&.isViewAll {
			background-color: #cc6900;
		}
	}
}
