.styleDataRange {
    margin: 0;

    border-radius: 3px;
    border: 1px solid var(--bgThemeBorder);
    color: var(--bgThemeTitle);
    background: var(--bgTheme);
}

.p-datepicker * {
    font-size: 1rem;
    font-family: 'Inter', sans-serif;
    font-weight: 500;
}

.p-datepicker:not(.p-datepicker-inline) {
    transform: translate(-4%, 2%);

    padding: 0.5rem;

    border: 1px solid var(--bgThemeBorder);
    border-radius: 3px;
    box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.2), 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12);

    color: var(--bgThemeTitle);
    background-color: var(--bgTheme);
}

.p-disabled, .p-component:disabled {
    opacity: 0.6;
}

.p-datepicker .p-datepicker-header {
    padding: 0.5rem;

    border-bottom: 1px solid var(--bgThemeBorder);
}

.p-datepicker .p-datepicker-header .p-datepicker-prev,
.p-datepicker .p-datepicker-header .p-datepicker-next {
    width: 2rem;
    height: 2rem;

    border: 0 none;
    border-radius: 50%;

    color: #6c757d;
    background-color: var(--bgTheme);
}

.p-datepicker .p-datepicker-header .p-datepicker-prev:enabled:hover,
.p-datepicker .p-datepicker-header .p-datepicker-next:enabled:hover,
.p-datepicker .p-datepicker-header .p-datepicker-title .p-datepicker-year:enabled:hover,
.p-datepicker .p-datepicker-header .p-datepicker-title .p-datepicker-month:enabled:hover {
    color: #FF992C;
}

.p-datepicker .p-datepicker-header .p-datepicker-title .p-datepicker-year,
.p-datepicker .p-datepicker-header .p-datepicker-title .p-datepicker-month {
    padding: 0.5rem;
    color: var(--bgThemeTitle);
}

.p-datepicker table {
    margin: 0.5rem 0;
}

.p-datepicker table th,
.p-datepicker table td {
    padding: 0.5rem;
}

.p-datepicker table th > span,
.p-datepicker table td > span {
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 50%;
}

.p-datepicker table td > span.p-highlight,
.p-datepicker .p-monthpicker .p-monthpicker-month.p-highlight {
    background: hsla(31, 100%, 59%, 80%);;
}

.p-datepicker table td > span:focus {
    box-shadow: 0 0 0 0.2rem #FF992C;
}

.p-datepicker table td.p-datepicker-today > span {
    border: 2px solid #FF992C;
}

.p-datepicker .p-yearpicker,
.p-datepicker .p-monthpicker {
    margin: 0.5rem 0;
}

.p-datepicker .p-yearpicker .p-yearpicker-year,
.p-datepicker .p-monthpicker .p-monthpicker-month {
    padding: 0.5rem;
    border-radius: 3px;
}

.p-datepicker:not(.p-disabled) .p-monthpicker .p-monthpicker-month:not(.p-disabled):not(.p-highlight):hover,
.p-datepicker:not(.p-disabled) .p-yearpicker .p-yearpicker-year:not(.p-disabled):not(.p-highlight):hover,
.p-datepicker:not(.p-disabled) table td span:not(.p-highlight):not(.p-disabled):hover{
    background: var(--bgButtonTextIcon);
}