@import '../../../../Theme/colors';
@import '../../../../Theme/light-dark-theme';

.header {
	display: flex;
	align-items: center;

	padding: 10px 10px 20px;

	font-size: 25px;
	font-family: 'Inter';

	border-bottom: 1px solid #d3d3d3;

	color: var(--colorText);
}
.modBtn{
	width: 100%;
	height: 100%;

	border-radius: 0;
	border: none;
	box-shadow: none;

	background: #FF992C;
	color: #fff;

	font-size: 16px;
	font-family: Inter;

	cursor: pointer;
}
.modBtn:hover{
	background: #CC6900;
}

.button {
	width: calc(100% - 20px);
	height: 45px;

	position: absolute;
	bottom: 20px;
}

.editName {
	margin: 20px 20px 24px;
	font-size: 30px;
	font-family: 'Inter';
}

.edit {
	margin: 20px;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.mainEdits {
	height: 75%;

	margin-bottom: 10px;
	padding: 0 20px;

	font-family: 'Inter';
	font-size: 16px;
}

.pen {
	width: 15px;
	height: 15px;
	margin: 0 10px 3px;
	cursor: pointer;
}

.fieldText {
	width: 100%;
	padding: 5px;
	box-sizing: border-box;
	font-size: 16px;
	border: 1px solid #cfcfcf;
	text-overflow: ellipsis;
}

.ListCameras {
	width: 100%;
	height: 200px;

	overflow: auto;

	border: 1px solid #cfcfcf;
	background: var(--bgTheme);
	color: var(--colorText);
}

.List {
	display: flex;
	justify-content: space-between;
	align-items: center;

	position: relative;
	margin: 0;
	padding: 0px 5px;

	border: 0;
	border-radius: 0;
	
	outline: 0;
	vertical-align: middle;
	text-decoration: none;
	
	background-color: transparent;

	cursor: pointer;
}

.allSelect {
	height: 28px;

	display: flex;
	justify-content: space-between;
	align-items: center;

	padding: 0px 5px;

	cursor: pointer;
}

.fieldText:hover {
    border: 1px solid #cfcfcf;
}

.fieldTextEdit {
	width: 80%;
	padding: 10px;
	box-sizing: border-box;
	font-size: 18px;
	border: 1px solid #cfcfcf;
}

.nameInput {
	margin: 10px 0;
	font-size: 14px;
	color: var(--colorText);
}

.editSize {
	display: inline-block;
	margin: 20px 0;
	color: #ff992c;
	cursor: pointer;
}

.deleteEdit {
	display: inline-block;

	color: #cc0303;
	cursor: pointer;
}
