@import '../../globalStyles/vars.module.scss';
.parent {
  width: 100%;
  height: calc(100% - $header-height);

  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;

  position: absolute;
  top: $header-height ;
  left: 0;
  bottom: 0;
  z-index: 1;

  background: var(--bgThemeColor1);
  color: var(--colorText);
}

.pdf {
	width: 100%;
	height: 100%;

	position: absolute;
	top: 0;
	left: 0;
	z-index: 1;
	background: var(--bgThemeColor1);
	color: var(--colorText);
}
.pdfBtn {
	position: absolute;
	top: 20px;
	left: 0;
	z-index: 2;
}

.page {
	width: 100%;
	height: 100%;

	display: flex;
	justify-content: center;
	align-items: center;

	padding: 0 20px;
	background: var(--bgThemeColor1);
}
