@import '../../globalStyles/vars.module.scss';

.cameraVievPage {
	width: 100vw;
	height: 100vh;

	position: relative;

	.menu01 {
		position: fixed;

		top: calc($header-height + 2.5%);
		bottom: 2.5%;

		&.left {
			left: 0;
		}
		&.right {
			right: 0;
		}

		z-index: 2;

		.item {
			margin: 4px 8px;
		}
	}
}
.menu02 {
	display: flex;

	margin-top: calc($header-height);
}
.newContainer {
	width: 100vw;
	height: 100vh;

	display: flex;
	flex-direction: row;
	justify-content: space-between;
	background: none;
}

.modalAddPresetBox {
	position: absolute;
	top: 50%;
	left: 50%;

	transform: translate(-50%, -50%);
	z-index: 2;
}

.container {
	width: 100%;
	height: calc(100% - $header-height);

	padding-top: $header-height;

	overflow: hidden;
	position: relative;
	display: flex;
}

.canvasBox {
	width: 100%;
	height: 100%;

	display: flex;
	align-items: center;
	justify-content: center;

	position: absolute;
	top: 0;
	left: 0;
	z-index: 1;
}

.tempButton {
	color: #fff;
	font-family: 'Inter', sans-serif;
	font-size: 20px;

	border: none;
	padding: 8px 16px;
	background-color: hsl(31, 100%, 60%);
	user-select: none;

	position: absolute;
	top: 35px;
	left: 50%;
	z-index: 20;

	transform: translate(-50%, -50%);
}

.video {
	width: 100%;
	height: 100%;

	position: absolute;
	top: 0;
	left: 0;
	z-index: 1;
}
