.item {
  height: 85px;

  display: flex;
  justify-content: space-between;

  background: var(--bgTheme);
  border-bottom: 1px solid var(--bgThemeBorder);
}

.navLink {
  width: 100%;

  display: flex;
  justify-content: space-between;
  align-content: center;

  color: var(--colorTextNotifications);
  text-decoration: none;

  padding-right: 5px;
}
.navLink:hover {
  color: #FF992C;
}
.listTitle{
  width: 50%;

  align-content: flex-start;
  font-family: Inter, serif;
  font-size: 20px;
  font-weight: 600;

  color: var(--colorTextTitleNotifications);
}
.dateText{
  width: 170px;
  height: 48px;

  display: flex;
  align-items: center;

  text-align: right;
}
.descriptionText{
  width: 256px;
  height: 48px;

  display: flex;
  align-items: center;

  margin-right: 6px;
}
.navLinkWatch {
  width: 50%;

  display: flex;
  justify-content: flex-end;
  text-align: end;

  color: #888888;
  font-family: Inter, serif;
  font-weight: 600;
  font-size: 16px;

  text-decoration: none;
}
.navLinkWatch:hover {
  color: #FF992C;
}

.listSubheader {
  height: 60px;

  font-family: Inter, serif;
  font-size: 16px;
}
.listItem{
  padding: 0;
}
.listItem{
  height: 60px;

  display: flex;
  justify-content: space-between;
}

.title {
  font-size: 18px;
  font-weight: 600;
  font-family: Inter, serif;
  color: #444;
}
.subTitle {
  font-size: 14px;
  font-weight: 400;
  font-family: Inter, serif;
  color: #666;
}
.additionalDescription {
  display: flex;

}
.listItemIcon{
  display: flex;
  justify-content: flex-start;
}
.btnCorrectPosition {
  display: flex;
  justify-content: center;
}
.conditionalStyle{
  width: 21px;
  height: 21px;

  border-radius: 10px;
}