@import '../../../Theme/colors';
@import '../../../Theme/light-dark-theme';

.radarMenu {
	width: 100%;
	height: 100%;

	max-width: 332px;

	display: flex;
	flex-direction: column;

	position: relative;

	background-color: var(--bgTheme);
}

.switchItem {
	display: flex;
	align-items: center;
	border: 1px solid var(--bgThemeBorder);

	cursor: pointer;
}

.btnPos {
	position: absolute;

	top: 6.1%;
	&.left {
		left: 100%;
	}
	&.right {
		right: 100%;
	}

	z-index: 1;

	.iconButton {
		width: 50px;
		height: 50px;

		background-color: hsl(218, 15%, 65%);

		border: 1px solid hsl(218, 20%, 77%);
		border-radius: 0;

		&:hover {
			background-color: hsl(31, 100%, 80%);
		}

		&.active {
			background-color: hsl(31, 100%, 60%);
			transform: rotate(180deg);
		}
	}
}

.container {
	width: 312px;
	height: 100%;

	display: flex;
	flex-direction: column;

	margin: 28px 10px 10px 10px;
	font-family: Inter;

	.footerBox {
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
	}

	.titleBox {
		display: flex;
		justify-content: space-between;

		.menuTitle {
			font-size: 26px;
			font-family: Inter;
			color: var(--colorText);
		}

		.iconButton {
			border-radius: 0;
			color: var(--bgThemeButtonIcon);
		}
	}
}

.item {
	display: flex;

	padding: 12px 21px 12px 10px;

	.itemText {
		font-family: 'Inter';
		font-weight: 400;
		font-size: 18px;
		color: var(--colorText);
	}
}
