.block {
    position: absolute;
    display: flex;
    width: 100%;
    height: 100%;
    background: #CFCFCF;
    border: 1px solid #CFCFCF;
}

.video{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.timeContrl{
    display: flex;
}

.icons{
    font-size: 25px; 
    color: #FFFFFF;
    :active{
        color: #FF992C;
    }
}

.radar{
    position: absolute;
    height: 201px;
    width: 198.85px;
    top: 20px;
    right: 20px;
    z-index: 100;
}