.selectHeader {
	height: 20px;

	display: flex;
	justify-content: center;
	align-items: center;

	font-weight: 600;

	color: var(--colorText);

	cursor: pointer;
}
.container {
	display: flex;
	justify-content: flex-end;
	align-items: center;

	margin-right: 20px;

	font-family: 'Inter', serif;
}

.containerFilter {
	display: flex;
	flex-direction: column;
}

.title {
	display: flex;
	justify-content: center;
	align-items: flex-end;
}

.listArchive {
	max-height: 321px;
	width: 320px;

	position: absolute;
	bottom: 80px;
	right: 15px;
	z-index: 10;

	overflow: auto;

	background: var(--bgTheme);
}
.btnListFilter {
	display: flex;
	justify-content: space-between;
	align-items: center;

	padding: 10px;

	font-family: 'Inter';
	font-style: normal;
	font-weight: 500;
	font-size: 16px;
	line-height: 24px;

	cursor: pointer;
}

.search {
	width: 98%;
	height: 44px;

	display: flex;
	justify-content: flex-start;
	align-content: center;
	align-items: center;

	background: var(--bgThemeSearch);
	color: var(--colorText);

	padding-left: 4px;
	margin-bottom: 15px;
}
.rightField {
	font-family: Inter, serif;
	font-weight: 500;
	font-size: 18px;
	color: #cfcfcf;

	margin-right: 10px;
}

.expandIcon {
	width: 25px;
	height: 20px;
}
