.iconHidden {
    width: 14px;
	height: 9px;

	cursor: pointer;
}
.btnViewZones{
    width: 260px;
    height: 50px;

    display: flex;
    justify-content: space-between;
    align-items: center;

    position: absolute;
    left: 40%;
    padding: 5px 15px;

    border-radius: 0;
    box-shadow: none;

    transform: rotate(-180deg);

    color: #fff;
    background: #FF992C;
    border: none;

    font-size: 16px;
    font-family: Inter;

    cursor: pointer;

    &:hover {
        background: #F07C00;
    }
};

.boxHidden{
    width: 30px;
    height: 30px;

    display: flex;
    justify-content: center;
    align-items: center;
}

.visibleBox {
    width: 260px;
    height: 100px;

    position: absolute;
    left: 40%;
    top: 80%;

    font-size: 18px;

    transform: rotate(-180deg);

    border: 1px solid #A8A8A8;

    color: rgba(71, 84, 103, 1);
    background-color: white;
}

.viewIcon {
    width: 30px;
    height: 30px;
}