@import '../../Theme/colors';
@import '../../Theme/light-dark-theme';

.searchMenu {
	width: 100%;
	height: 100%;

	max-width: 332px;

	display: flex;
	flex-direction: column;

	position: relative;

	background-color: var(--bgTheme);
}

.btnPos {
	position: absolute;

	top: 6.1%;
	&.left {
		left: 100%;
	}
	&.right {
		right: 100%;
	}

	z-index: 1;

	.iconButton {
		width: 50px;
		height: 50px;

		background-color: hsl(218, 15%, 65%);

		border: 1px solid hsl(218, 20%, 77%);
		border-radius: 0;

		&:hover {
			background-color: hsl(31, 100%, 80%);
		}

		&.active {
			background-color: hsl(31, 100%, 60%);
			transform: rotate(180deg);
		}
	}
}

.container {
	width: 312px;
	height: 100%;

	display: flex;
	flex-direction: column;

	margin: 28px 10px 10px 10px;
	font-family: Inter, sans-serif;

	.footerBox {
		display: flex;
		flex-direction: column;
	}

	.titleBox {
		display: flex;
		justify-content: space-between;

		padding-bottom: 20px;
		position: relative;

		.menuTitle {
			font-size: 26px;
			font-family: Inter;
			color: var(--colorText);
		}

		.iconButton {
			border-radius: 0;
			color: var(--bgThemeButtonIcon);
		}

		&::after {
			height: 1px;

			content: '';

			position: absolute;
			bottom: 0px;
			left: -10px;
			right: -10px;
			z-index: 1;

			background-color: var(--bgThemeBorder);
		}
	}

	.divider {
		background-color: var(--bgThemeBorder);
	}

	.textField {
		padding: 5px 8px 5px 12px;
		border-radius: 0;
		background-color: #fff;
	}

	.emptyBox {
		width: 100%;

		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	.list {
		width: 100%;

		padding: 6px 0px;
		overflow-y: auto;

		.item {
			width: 100%;
			flex-grow: 1;

			padding: 8px 0px;

			.workIndicator {
				width: 8px;
				height: 8px;

				content: none;
				margin: 0 10px;

				border-radius: 50%;
			}

			.itemText {
				font-family: Inter;
				font-weight: 500;
				color: var(--colorText);
			}

			.itemIcon {
				justify-content: right;
				margin: 0 10px;
			}
		}
	}
}
