@import '../../../../globalStyles/vars.module.scss';
.page{
  width: 100%;
  height: calc(100% - $header-height);

	display: flex;
	justify-content: space-between;

	padding: 5px 20px;
	margin-top: 15px;
	background: var(--bgThemeColor1);
}
.gridContainer {
	width: 49.5%;

	display: flex;
	flex-direction: column;
	justify-content: space-between;
}
.cardBox {
	width: 49%;
}
.cardPersonBox {
	height: 49%;
}
.cardsBox {
	height: 49%;

	display: flex;
	justify-content: space-between;
}
.gridBox {
	width: 49.5%;

	display: flex;
	flex-direction: column;
	overflow: auto;
}

.guideBtn {
	font-family: 'Inter', sans-serif;
	line-height: 24px;
	letter-spacing: 0.17px;
	text-transform: uppercase;

	cursor: pointer;

	color: var(--colorText);

	padding: 10px 12px;
}

.button {
	color: #ff992c;
}

.wrapper {
	height: calc(100% - 60px);

	overflow: auto;
	background: var(--bgThemeColor1);
}
.table {
	width: 100%;
	height: 100%;

	overflow: auto;
	font-family: 'Inter', sans-serif;
	line-height: 24px;
	letter-spacing: 0.17px;
	text-transform: uppercase;
	background: var(--bgTheme);
}

.changeColor:not(:hover) {
	filter: saturate(0%);
	-webkit-filter: saturate(0%);
}
.btnTitle {
	font-family: Inter;
	font-style: normal;
	font-weight: 500;
	font-size: 14px;

	margin-right: 15px;

	cursor: pointer;
}

.smallTable {
	width: 100%;
	height: 100%;

	display: flex;
	justify-content: center;
	align-items: center;

	background: var(--bgTheme);
	color: #cfcfcf;
}
.title {
	font-weight: 600;
	font-size: 24px;
	line-height: 30px;
	margin-left: 15px;

	color: var(--colorText);
}

.head {
	height: 58px;
	width: 100%;

	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;

	position: relative;
	font-family: 'Inter', sans-serif;
	font-weight: 500;
	font-size: 18px;
	color: #494949;
	background: var(--bgTheme);
	border-bottom: 0.5px solid var(--bgThemeBorder);
}
.iconSettings {
	width: 22px;
	height: 22px;
}
.themeCard {
	width: max-content;
	height: max-content;

  position: absolute;
  top: 70px;
  right: 5px;
  z-index: 0;

	background: var(--bgTheme);
	border: 2px solid var(--bgThemeBorder);
}
.changeColor:not(:hover) {
	filter: saturate(0%);
	-webkit-filter: saturate(0%);
}
