.listArchive * {
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    font-size: 16px;
}

.listArchive {
    height: 250px;
    width: 344px;

    position: absolute;
    left: -1px;
    top: 44px;

    overflow: auto;
    background: var(--bgTheme);
    border: 1px solid var(--bgThemeBorder);
}

.search {
    height: 37px;

    display: flex;
    align-items: flex-end;

    padding: 0 0 10px 10px;
    border-bottom: 1px solid var(--bgThemeBorder);

    .searchText {
        color: var(--bgThemeTitle);
    }
}

.listDevice {
    height: 25px;
    display: flex;
    align-items: center;

    margin: 10px;
    cursor: pointer;
}

.point {
    width: 8px;
    height: 8px;

    border-radius: 50%;
    margin-right: 10px;
    background: #12B76A;
}