.container {
	display: flex;
	align-items: center;

	font-family: 'Inter';
	font-style: normal;
	font-weight: 500;
	font-size: 18px;
	line-height: 28px;
}

.selectBtn {
	width: 102px;
	height: 28px;

	color: #cfcfcf;
}

.selectText {
	width: 132px;
	height: 28px;

	color: #5f5f5f;
}

.reset {
	cursor: pointer;
}

.selectAll {
	width: 163px;
	height: 28px;

	display: flex;
	justify-content: center;
	margin-right: 15px;

	color: #ff992c;

	cursor: pointer;
}

.delete {
	width: 109px;
	height: 28px;

	color: #ff2c2c;

	cursor: pointer;
}
