.titleBox {
    padding: 12px 21px 6px 10px;
}

.title {
	font-family: Inter, sans-serif;
	font-weight: 500;
	font-size: 18px;
	color: var(--colorText);

	line-height: 1.5;
    letter-spacing: 0.00938em;
}

.polygonMenu {
	height: 100%;

	display: flex;
	flex-direction: column;
	justify-content: space-between;

	.confirmButtonsPos {
		margin-bottom: 10px;
	}
}

.list {
	max-height: calc(100% - 45px);
	overflow: auto;

	display: flex;
	flex-direction: column;

	.listItemIcon {
		display: flex;
		justify-content: flex-end;

		transition: opacity 0.2s;

		&.settings {
			opacity: 0;

			&:hover {
				.editText {
					color: hsl(31, 100%, 60%);
				}
			}
		}
	}

	.switchItem {
		display: flex;
		align-items: center;
		border: 1px solid var(--bgThemeBorder);
	}
	
	.editText {
		font-family: Inter, sans-serif;
		font-size: 10px;
		color: #B8B8B8;
		
		user-select: none;
		cursor: pointer;
	}
	.settingsItem {
		&:hover {
			.listItemIcon.settings {
				opacity: 1;
			}
		}
	}

	.roundIndicator {
		width: 20px;
		height: 20px;

		border-radius: 50%;
		box-shadow: inset rgba(0, 0, 0, 0.6) 0 -3px 8px, inset rgba(252, 255, 255, 0.7) 0 3px 8px,
			rgba(0, 0, 0, 0.8) 0 3px 8px -3px;

		margin-right: 10px;
	}

	.recName {
		font-family: Inter, sans-serif;
		font-weight: 400;
		font-size: 16px;
		color: var(--colorText);
		color: hsl(0, 0%, 75%)
	}
}

.information {
	font-weight: 400;
	font-size: 12px;
	color: var(--colorText);
}


.confirmButtons {
	display: flex;

	gap: 12px;

	.save {
		height: 54px;

		flex-grow: 1;

		border-radius: 0;

		color: #fff;
		font-size: 20px;
		font-family: Inter;

		background-color: hsl(31, 100%, 60%);

		&:hover {
			background-color: hsl(31, 100%, 40%);
		}
	}

	.cansel {
		height: 54px;

		flex-grow: 1;

		border-radius: 0;

		color: #989898;
		font-size: 18px;
		font-family: Inter;

		background-color: var(--bgTheme);

		&:hover {
			background-color: hsl(31, 100%, 80%);
		}
	}
}

.emptyMessage {
	width: 100%;
	height: 100%;

	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	gap: 8px;

	text-align: center;

	font-weight: 400;
	font-family: 'Inter';
	text-transform: uppercase;

	.erTitle {
		font-size: 22px;
		color: var(--colorText);
	}

	.erMessage {
		font-size: 17px;
		color: hsl(31, 100%, 60%);
		cursor: pointer;
	};
}