.choose {
    display: flex;
	flex-direction: column;
    
	position: absolute;
	
	margin: 10px;
	font-size: 18px;
}

.chooseCamera {
	display: flex;
	align-items: center;
	cursor: pointer;
}

.chooseCamera:hover .moreIcon {
	display: block;
}

.selectHeader {
	padding: 5px 10px;
	font-family: 'Inter';
	font-weight: 600;
	font-size: 18px;
	color: white;
}

.ListCameras {
	width: 200%;
	height: 500%;
	background: var(--bgTheme);
	position: absolute;
	top: 43px;
	overflow: auto;
	z-index: 1000;
}

.SearchCameras {
	width: 100%;
	height: 44px;

	display: flex;

	background: var(--bgThemeSearch);
}

.moreIcon {
	display: none;
	width: 14px;
	height: 7px;
	z-index: 100;
}

.List {
	border: 0;
	margin: 0;
	display: flex;
	outline: 0;
	padding: 10px;
	justify-content: space-between;
	position: relative;
	align-items: center;
	user-select: none;
	border-radius: 0;
	vertical-align: middle;
	text-decoration: none;
	background-color: transparent;
	cursor: pointer;
}

.List:hover {
	background-color: #e4e4e4;
	.edit {
		display: block;
	}
}

.edit {
	display: none;
	font-size: 12px;
	margin-right: 10px;
	font-family: 'Inter';
	color: #a4a4a4;
}

.workImg {
	width: 14px;
	height: 14px;

	z-index: 100;
}