.parent {
	width: 100%;
	height: 70px;

	display: flex;
	flex-direction: row;
	justify-content: space-between;

	position: fixed;
	bottom: 0;
	right: 0;
	z-index: 1000;

	background-color: var(--bgTheme);
	box-shadow: 0 0 0 4px var(--bgThemeBorder);
}

.boxCard {
	width: 320px;
	height: max-content;

	position: absolute;
	bottom: 85px;
	left: 15px;
	z-index: 100500;

	background: var(--bgTheme);
	border: 1px solid var(--bgThemeBorder);
	box-shadow: 0 12px 16px -4px rgba(16, 24, 40, 0.08), 0px 4px 6px -2px rgba(16, 24, 40, 0.03);
}

.text {
	font-family: 'Inter', sans-serif;
	font-size: 16px;
}

.leftContainer {
	width: 365px;

	display: flex;
	justify-content: left;
	align-items: center;

	margin-left: 15px;
}
.btnIcons {
	width: 48px;
	height: 39px;

	display: flex;
	justify-content: center;
	align-content: center;

	padding-top: 7px;
	padding-left: 1px;
	margin-right: 3px;
	background: var(--bgThemeButton1);

	border: 2px solid #cfcfcf;
	border-radius: 8px;

	cursor: pointer;
}
.stylesButtonArchActive {
	width: 130px;
	height: 48px;

	display: flex;
	justify-content: space-between;
	align-items: center;

	padding: 5px 16px 5px 8px;
	gap: 7px;

	margin: 0 5px;
	box-sizing: border-box;
	border: 1px solid var(--bgButtonTextIcon);
	border-radius: 9px;

	font-family: Inter;
	font-style: normal;
	font-weight: 500;
	font-size: 16px;
