.textPage{
  width: max-content;
  margin: 0 10px 0 10px;

  font-family: 'Inter', sans-serif;
  font-size: 16px;
  color: var(--bgThemeTitle);
}

.centerContainer{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.arrowsIconBtn{
  width: 48px;
  height: 48px;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 12px 16px 12px 18px;
  gap: 16px;
  background: var(--bgThemeButtonIcon);

  border: 2px solid var(--bgButtonBorder);
  border-radius: 8px;
  color: #CFCFCF;

  cursor: pointer;
}
.arrowsIconBtnDisabled{
  width: 48px;
  height: 48px;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 12px 16px 12px 18px;
  gap: 16px;
  background: var(--bgThemeButtonIcon);

  border: 2px solid var(--bgButtonBorder);
  border-radius: 8px;
  color: #CFCFCF;
}

.arrowsIconBtn:hover {
  background: var(--bgThemeButtonIcon);
  color: #FF992C;
}