.arrowButton {
    width: 62px;
    height: 62px;
    
    .iconButton {
        width: 100%;
        height: 100%;

        border-radius: 4px;
        background-color: #D9D9D9;

        .icon {
            width: 100%;
            height: 100%;

            display: flex;
            justify-content: center;
            align-items: center;

            &.top {
                transform: rotate(-90deg);
            }
        
            &.right {
                transform: rotate(0deg);
            }
        
            &.bottom {
                transform: rotate(90deg);
            }
        
            &.left {
                transform: rotate(180deg);
            }
        }
        

        &:hover {
            background-color: hsl(31, 100%, 60%);
        }
    }
}