.navButtons {
	margin-left: 26px;
	display: flex;
	flex-direction: row;
}

.linkBox {
	width: 50%;
	height: 36px;

	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
}

.btnLinkHeader {
	height: 36px;

	border: 1px solid var(--colorHeader);
}

.NavLinkItem {
	display: flex;
	flex-direction: row;
	align-items: center;

	font-weight: bold;
	text-decoration: none;
}
.toolBarStyle {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0 30px;
}
.btnIconStyle {
	margin: 0 10px;

	font-size: 16px;
	font-family: HalvarBreit;
	color: var(--colorText);
}

.navButtons__item {
	margin-right: 20px;
	font-weight: 400;
	text-decoration: none;
	color: inherit;
	text-transform: uppercase;
}

.leftItemsContainer {
	display: flex;
	flex-direction: row;
	align-items: center;
}

.navButtons__item:after {
	content: '';
	width: 100%;
	height: 2px;
	background-color: #fff;
	display: flex;
	opacity: 0;

	transition: opacity 0.2s linear;
}

.navButtons__item:hover:after {
	opacity: 1;
}

.navButtonsHome {
	font-size: 22px;
	font-weight: 400;

	margin: 0 10px;
	text-decoration: none;
	color: inherit;
}

.menu__btn {
	text-decoration: none;
	color: inherit;
}

.header__icons {
	color: #fff;
}

.header_profile__icon {
	width: 40px;
	height: 40px;
	border-radius: 50%;
}
