.container {
	height: 100%;

	font-family: 'Inter';
	font-size: 15px;
	color: var(--colorText);
}

.header {
	display: flex;
	justify-content: space-between;
	align-items: center;

	font-size: 25px;
	color: var(--colorText);

	padding: 10px 10px 20px;
}

.ships {
	height: 35%;

	display: flex;
	flex-wrap: wrap;
	flex-direction: column;

	margin: 0 20px;
}

.shipItem {
	width: 50%;
	height: 13%;

	display: flex;
	align-items: center;

	margin: 10px 10px 10px 0;
}

.zones {
	height: 30%;
	
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	justify-content: space-around;

	margin: 0 20px;
}

.zoneItem {
	margin: 10px 10px 10px 0;
	display: flex;
	align-items: center;
}

.lower {
	height: 30%;
	
	display: flex;
	flex-direction: column;
	justify-content: space-around;

	margin: 0 20px;
}

.closeIcon {
	width: 20px;
	height: 20px;

	cursor: pointer;
}

.shipIcons {
	width: 46px;
	height: 21px;

	margin-right: 10px;
}

.lineIcons {
	width: 80px;
	height: 1px;

	margin-right: 10px;
}

.zoneIcons {
	width: 80px;
	height: 30px;

	margin-right: 10px;
}

.cameraIcons {
	width: 34px;
	height: 20px;

	margin-right: 10px;
}

.lowerItems {
	width: 38px;
	height: 38px;

	margin-right: 10px;
}

.divider {
	background-color: var(--bgThemeBorder);
}