.btns {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.settings {
    width: 320px;

    display: flex;
    align-items: center;
    justify-content: space-between;

    position: absolute;
    right: 35px;
    top: 48px;
    z-index: 1000;

    padding: 10px 12px;

    background: var(--bgTheme);
    border: 1px solid var(--bgThemeBorder);

    font-family: 'Inter', sans-serif;
    font-weight: 400;
    font-size: 16px;
}