.confirm {
    margin: 0 100px;
    
    transform: rotate(-180deg);
}
.modBtn {
    width: 200px;
    height: 50px;

    border-radius: 0;
    box-shadow: none;
    margin: 10px;

    color: #fff;
    background: #FF992C;
    border: none;

    font-size: 16px;
    font-family: Inter;

    cursor: pointer;

    &:hover {
        background: #F07C00;
    }
}

.resetBtn {
    width: 200px;
    height: 50px;

    border-radius: 0;
    box-shadow: none;
    margin: 10px;

    color: #fff;
    background: #CFCFCF;
    border: none;

    font-size: 16px;
    font-family: Inter;

    cursor: pointer;

    &:hover {
        background: #999999;
    }
}