.cardField {
    width: calc(100% - 60px);
    height: calc(100% - 150px);

    position: absolute;

    margin: 0 30px;
    overflow: auto;

    background: var(--bgTheme);
    border: 0.5px solid var(--bgThemeBorder);
}

.modalHead {
    width: 100%;
    height: 58px;

    display: flex;
    align-items: center;
    justify-content: space-between;

    font-family: 'Inter', sans-serif;
    font-weight: 500;
    font-size: 18px;
    color: var(--bgThemeTitle);
    border-bottom: 1px solid var(--bgThemeBorder);
}

.nameModal {
    margin: 15px;
}

.icons {
    width: 130px;
    margin-right: 10px;
}

.mapWithTable {
    height: 88%;

    display: flex;
    overflow: hidden;
}

.wrapper {
    width: 45%;
    height: calc(100% - 40px);

    margin: 20px 0 0 20px;
    overflow-y: scroll;
}

.map {
    width: 55%;
    margin: 20px;
}

.chart {
    width: 100%;
    height: calc(100% - 75px);
}

.noData {
    height: calc(100% - 60px);

    display: flex;
    justify-content: center;
    align-items: center;

    margin: 0;

    font-weight: 500;
    font-size: 24px;
    letter-spacing: 2.17px;
    text-transform: uppercase;
    color: #CFCFCF;
}