@import '../../globalStyles/vars.module.scss';

.radar {
	position: fixed;

	width: 100%;
	height: calc(100% - 80px);

	display: flex;
	justify-content: center;

	top: 80px;
	left: 0;

	z-index: 100;
	opacity: 0.95;
	background-color: black;
}

.imgRadar {
	width: 800px;
}

.parent {
	width: 100vw;
	height: 100vh;

	.menu {
		width: 330px;

		top: calc($header-height + 2.5%);
		bottom: 2.5%;

		position: fixed;
		overflow: auto;

		z-index: 1001;
		background-color: rgb(255, 255, 255);

		&.right {
			right: 0;
		}

		.item {
			margin: 4px 8px;
		}
	}

	.menu02 {
		position: fixed;

		top: $header-height;
		bottom: 0;
		right: 0;

		z-index: 1001;

		background-color: white;
	}
}

.container {
	width: 100%;
	height: calc(100% - $header-height);

	padding-top: $header-height;
}

.block {
	width: 100%;
	height: calc(100vh - 64px);

	margin-top: 64px;

	background-color: var(--bgTheme);
	box-shadow: 0px 0px 0px 4px var(--bgThemeBorder);
	filter: drop-shadow(4px 4px 4px rgba(0, 0, 0, 0.4));
}

.control {
	width: 20%;
	height: auto;

	display: flex;
	flex-direction: column;

	border: 1px solid #d9d9d9;
	border-radius: 5px;

	margin: 20px 20px 20px 0;
}

img {
	width: 100%;
	height: 100%;
}

.bar {
	width: 60px;
	height: calc(100vh - 60px);

	margin-left: auto;
	margin-top: 60px;

	background-color: #fff;
	overflow: auto;
}

.rightMenu {
	width: 60px;
	height: 60px;

	display: flex;
	justify-content: center;
	align-items: center;
}

.rightMenuCur {
	width: 60px;
	height: 60px;

	display: flex;
	justify-content: center;
	align-items: center;

	background-color: #d8d8d8;
}

.scale {
	width: 30px;
	height: 60px;

	position: absolute;

	top: 10px;
	right: 10px;

	display: flex;
	flex-direction: column;

	border-radius: 3px;

	background-color: var(--bgThemeColor1);
	color: var(--colorText);
}


.errorMessagePosition {
	position: absolute;
	top: calc($header-height + 13px);
	left: 50%;
	transform: translateX(-50%);
	z-index: 10000;

	span {
		white-space: pre;
		user-select: none;
	}

	.goToLookOut {
		color: hsl(31, 100%, 94%);
		text-decoration: none;

		&:hover {
			color: hsl(31, 100%, 60%);
		}
	}
}