.Table {
  width: 100%;
  height: 100%;

  font-family: 'Inter', sans-serif;
  line-height: 24px;
  letter-spacing: 0.17px;
  text-transform: uppercase;

  background: var(--bgTheme);
}
.selectHeader{
  font-family: 'Inter', serif;
  font-weight: 600;
  font-size: 18px;
  color: var(--colorText);

  z-index: 100;
}
.SearchCameras{
  width: 90%;
  height: 50px;

  display: flex;
  justify-content: center;
  align-items: center;

  background: var(--bgThemeSearch);
  border: 1px solid var(--bgThemeBorder);
}
.searchContainer {
  width: 100%;
  height: 50px;

  display: flex;
  justify-content: center;

  margin-bottom: 10px;
}

.listForShip{
  background: var(--bgTheme);
  margin-top: 10px;
  overflow-y: auto;
  padding-right: 10px;
  padding-left: 20px;
}

.listForDevice{
  width: 100%;

  justify-content: space-between;

  background: var(--bgTheme);
  margin-top: 10px;
  overflow-y: auto;
  padding-right: 10px;
  padding-left: 20px;
}

.imgConditional{
  width: 22px;
  height: 22px;

  border-radius: 10px;

  margin-right: 10px;
}
.containerIcons{
  width: max-content;
  height: 22px;

  display: flex;

  padding-bottom: 6px;
}
.iconPen{
  width: 15px;
  height: 15px;

  color: #cfcfcf;
}
.iconClose{
  width: 18px;
  height: 18px;

}
.imgShip{
  width: 157px;
  height: 81px;
}


.tableText{
  width: 100%;
  height: 35px;

  display: flex;
  justify-content: space-between;

  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
}
.changeColor:not(:hover) {
  -webkit-filter: saturate(0%);
}
