@import '../../globalStyles/vars.module.scss';

.monitorPage {
	width: 100vw;
	height: 100vh;

	.menu01 {
		position: fixed;

		top: calc($header-height + 2.5%);
		bottom: 2.5%;

		&.left {
			left: 0;
		}
		&.right {
			right: 0;
		}

		z-index: 1001;
		background-color: aqua;

		.item {
			margin: 4px 8px;
		}
	}
}

.container {
	width: 100%;
	height: calc(100% - $header-height);

	padding-top: $header-height;
}
