.lower{
    width: 100%;
    height: 100px;

    display: flex;
    align-items: center;
    
    position: absolute;
    bottom: 0;
    z-index: 1000;

    font-family: 'Inter';

    background: linear-gradient(180deg, rgba(0, 0, 0, 0.7) 0%, rgba(0, 0, 0, 0) 100%);
    transform: rotate(-180deg);
}

.iconButton {
    width: 46px;
    height: 46px;

    display: flex;
    align-items: center;
    justify-content: center;

    position: absolute;
    right: 0;
    margin: 30px;

    transform: rotate(-180deg);

    background-color: transparent;
    border: 1px solid white;
    cursor: pointer;

    &:hover {
        border: 1px solid #F07C00;
        background: #F07C00;
    }
}

.question{
    width: 50px;
    height: 50px;

    display: flex;
    align-items: center;
    justify-content: center;

    position: absolute;
    z-index: 1000;

    margin: 0 25px;
    border-radius: 50%;

    font-size: 34px;
    font-weight: 600;
    color: white;

    transform: rotate(-180deg);
    background: #FF992C;
    cursor: pointer;

    &:hover {
        background: #F07C00;
    }
}