.listArchive * {
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    font-size: 16px;
}

.listArchive {
    padding: 15px 10px 0 10px;

    position: absolute;
    top: 95px;
    z-index: 10;

    border: 0.5px solid var(--bgThemeBorder);
    background: var(--bgTheme);

    &.left {
        width: 300px;
        left: 107px;
    }

    &.right {
        width: 220px;
        left: 450px;
    }
}

.select {
    display: flex;
    align-items: center;
}

.listItem {
    display: flex;
    justify-content: space-between;
    height: 35px;
}