.iconStyle:not(:hover){
  -webkit-filter: saturate(0%);
}

.container{
  position: relative;
}

.list{
  width: 526px;
  height: 308px;

  background: var(--bgTheme);
  border: 0.5px solid var(--bgThemeBorder);
  box-shadow:none;
}
.popoverStyle{
  margin-top: 15px;
  box-shadow: none;
  z-index: 100500;
}

.listItem{
  height: 60px;

  display: flex;
  justify-content: space-between;

  border-bottom: 1px solid var(--bgButtonBorder);
}

.navLink {
  width: 100%;

  display: flex;
  justify-content: space-between;
  align-content: center;

  text-align: start;
  color: #444;
  text-decoration: none;

  padding-right: 15px;
}

.navLink:hover {
  color: #FF992C;
}

.listTitle{
  width: 50%;

  align-content: flex-start;
  font-family: Inter, serif;
  font-size: 20px;
  font-weight: 600;

  color: var(--colorTextNotifications);
}

.navLinkWatch {
  width: 50%;

  display: flex;
  justify-content: flex-end;
  text-align: end;

  color: #888888;
  font-family: Inter, serif;
  font-weight: 600;
  font-size: 16px;

  text-decoration: none;
}

.navLinkWatch:hover {
  color: #FF992C;
}