@import '../../../../../Theme/light-dark-theme';

.container {
	width: 460px;
	height: 340px;

	padding: 20px;

	background-color: var(--bgTheme);

	.contentPosition {
		height: 286px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}

	.modalTitle {
		padding-bottom: 18px;

		position: relative;

		color: var(--colorText);
		font-family: Inter;
		font-weight: 500;
		font-size: 24px;

		user-select: none;

		&::after {
			height: 1px;

			display: block;

			content: '';

			position: absolute;

			left: -20px;
			bottom: 0px;
			right: -20px;
			z-index: 2;

			background-color: var(--bgTheme);
		}
	}

	.inputLabel {
		color: var(--colorText);
		font-family: Inter;
		font-weight: 400;
		font-size: 14px;

		user-select: none;
	}

	.iconButton {
		padding: 0px;
		border-radius: 0;

		margin-right: 0;
	}

	.buttonBox {
		display: flex;
		flex-wrap: wrap;

		gap: 12px;

		.create {
			height: 54px;

			color: #fff;
			font-size: 20px;
			font-family: Inter;

			background-color: hsl(31, 100%, 60%);

			&:hover {
				background-color: hsl(31, 100%, 40%);
			}
		}

		.cansel {
			height: 54px;

			color: #989898;
			font-size: 18px;
			font-family: Inter;

			background-color: var(--bgTheme);

			&:hover {
				background-color: hsl(31, 100%, 80%);
			}
		}
	}
}
