@import '../../../../../Theme/colors';
@import '../../../../../Theme/light-dark-theme';

.header {
	display: flex;
	align-items: center;

	padding: 10px 10px 20px;

	font-size: 25px;
	font-family: 'Inter';

	border-bottom: 1px solid #d3d3d3;

	color: var(--colorText);
}

.warningDelete {
	margin: 20px;

	font-size: 22px;

	color: var(--colorText);
}

.editDel {
	margin: 0 20px;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.fieldTextDelete {
	width: 100%;
	padding: 10px;
	box-sizing: border-box;
	font-size: 18px;
	border: 1px solid #cfcfcf;

	color: var(--colorText);
	background-color: var(--bgTheme);
}

.notDelete {
	display: inline-block;
	margin: 20px;

	cursor: pointer;
	color: var(--colorText);

	&:hover {
		color: #ff992c;
	}
}

.delete {
	display: inline-block;

	margin: 0 20px;

	color: #A7A7A7;

	&.ok {
		color: #CC0303;
		cursor: pointer;
	}
}
