@import '../../../../Theme/light-dark-theme';

.additionalControlPanel {
    display: flex;

    background-color: var(--bgTheme);

    padding: 15px 0;
    margin: 0 -10px;

    border-top: 1px solid var(--bgThemeBorder);
    border-bottom: 1px solid var(--bgThemeBorder);

    .iconButtonBox {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-grow: 1;

        border-right: 1px solid var(--bgThemeBorder);

		&:last-of-type {
			border-right: none;
		}
    }

	.iconButton {
		padding: 10px auto;
        
        border-radius: 0;
	}
}
