.back{
    display: flex;

    margin: 20px; 

    position: absolute; 
    right: 0; 
    z-index: 1000; 

    font-size: 20px; 
    font-family: 'Inter';

    cursor: pointer;
}



.upper{
    width: 100%;
    height: 100px;

    display: flex;
    align-items: center;

    position: absolute;
    top: 0;
    z-index: 1000;

    background: linear-gradient(180deg, rgba(0, 0, 0, 0.7) 0%, rgba(0, 0, 0, 0) 100%);
}

.confirm{
    margin: 0 100px;
    
    transform: rotate(-180deg);
}

.imgClose {
    width: 30px; 
    height: 30px; 

    margin-left: 10px;
}