@import '../../../../../Theme/colors';
@import '../../../../../Theme/light-dark-theme';

.List {
	display: flex;
	justify-content: space-between;
	align-items: center;

	margin: 0;
	padding: 10px 20px;

	border: 0;
	border-radius: 0;

	outline: 0;

	position: relative;

	user-select: none;
	vertical-align: middle;

	text-decoration: none;
	background-color: transparent;

	cursor: pointer;
}

.List:hover {
	.edit {
		display: block;
	}
}

.switchItem {
	display: flex;
	align-items: center;
	border: 1px solid var(--bgThemeBorder);

	cursor: pointer;
}

.listItem {
	padding-left: 40px;
}

.block {
	display: flex;
	align-items: center;
}

.ship {
	margin-left: 20px;

	font-size: 18px;
	font-family: 'Inter';

	color: var(--colorText);
}

.mmsi {
	font-size: 12px;

	color: var(--colorText);
}

.iconCheck {
	width: 30px;
	height: 30px;
	
	cursor: pointer;
}

.edit {
	display: none;

	margin-right: 10px;

	font-size: 12px;
	font-family: 'Inter';

	color: #ff992c;
}