@import '../../../../Theme/colors';
@import '../../../../Theme/light-dark-theme';

.header {
	display: flex;
	justify-content: space-between;

	align-items: center;
	padding: 10px 10px 20px;

	font-size: 25px;
	font-family: 'Inter';
	color: var(--colorText);
}

.menu {
	width: 100%;
	height: 95%;
}
.modBtn{
	width: 100%;
	height: 100%;

	border-radius: 0;
	border: none;
	box-shadow: none;

	background: #FF992C;
	color: #fff;

	font-size: 16px;
	font-family: Inter;

	cursor: pointer;
}
.modBtn:hover{
	background: #CC6900;
}

.inputMenu {
	width: 90%;
	padding: 10px;
	margin: 10px;
	border: 1px solid #d0d5dd;
}

.button {
	width: calc(100% - 20px);
	height: 45px;

	position: absolute;
	bottom: 20px;
}

.closeIcon {
	width: 20px;
	height: 20px;

	cursor: pointer;
}

.divider {
	background-color: var(--bgThemeBorder);
}

.boxAutocomplite {
	display: flex;
	justify-content: space-between;
	align-items: center;
}